@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
html{
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

 /*WHOLE CONTAINER OF INFOSECTION AND FORM SECTION*/

 .myContent {
     height: 100vh;
 }

 /*INFO SECTION TAKES 50% of the entire height*/

 .infoSection {
     height: 50%;
     width: 100%;
 }

 /*FormSection Takes The 50% of the entire height*/

 .formSection {
     height: 50%;
     width: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
     font-family: 'Poppins';
 }

 /*PARENT is InfoSection, this takes 20% of the parent's height*/

 .infoIntroduction {
     height: 20%;
 }

 /*PARENT is InfoSection, this takes 80% of the parent's height*/

 .infoGrid {
     height: 80%;
     display: flex;
     justify-content: space-around;
     align-items: center;
 }

 /*Parent is infoGrid, this is a single Card*/

 .myCard {
     background-color: white;
     height: 90%;
     width: 28%;
     display: flex;
     flex-flow: column;
     justify-content: space-around;
     font-family: 'Poppins';
     font-size: 15px;
 }

 /*FORM STYLING*/

 .myForm {
     display: flex;
     flex-flow: column;
     align-items: center;
     justify-content: center;
     width: 70%;
     height: 90%;
     background-color: white;
     border-radius: 6px;
 }

 .formQuestion {
     width: 100%;
     height: 25%;
     display: flex;
     flex-flow: column;
     align-items: center;
     justify-content: center;
 }

 .formQuestion span {
     font-size: 30px;
 }

 .formRow {
     height: 25%;
     display: flex;
     flex-flow: column;
     align-items: center;
     justify-content: center;
     width: 80%;
 }

 .formRow label {
     height: 50%;
     width: 100%;
     display: flex;
     justify-content: start;
 }

 .formRow input {
     width: 100%;
     height: 50%;
 }

 .formRowButton {
     height: 25%;
     width: 80%;
     display: flex;
     justify-content: center;
     align-items: center;
 }

 .myTextBox {
     width: 80%;
     height: 100%;
     border-top: none;
     border-right: none;
     border-left: none;
     border-bottom: 2px solid #eee;
     outline: none;
     background-color: transparent;
     color: #DAA520;
 }

 .myTextBox::-webkit-input-placeholder {
     color: #DAA520;
 }

 .myTextBox:-ms-input-placeholder {
     color: #DAA520;
 }

 .myTextBox::-ms-input-placeholder {
     color: #DAA520;
 }

 .myTextBox::placeholder {
     color: #DAA520;
 }

 .redPlaceHolder::-webkit-input-placeholder {
     color: red
 }

 .redPlaceHolder:-ms-input-placeholder {
     color: red
 }

 .redPlaceHolder::-ms-input-placeholder {
     color: red
 }

 .redPlaceHolder::placeholder {
     color: red
 }

 .goButton {
     background-color: #DAA520;
     border: none;
     border-radius: 6px;
     padding: 15px;
     color: white;
     outline: none;
 }

 .goButton:active {
     background-color: white;
     border: 0.5px solid black;
     color: #DAA520;
 }

 @media screen and (max-width: 992px) {
     .myContent {
         height: 100vh;
     }
     .infoSection {
         height: 70%;
     }
     .infoGrid {
         display: flex;
         flex-flow: column;
         justify-content: space-around;
         align-items: center;
            
     }
     .formSection {
         height: 30%;
     }
     .myCard {
         font-size: 20px;
         width: 80%;
         height: 40%;
         margin-top: 20px;
         margin-bottom: 20px;
     }
     .myForm {
         width: 100%;
         height: auto;
         border-radius: 0;
     }
     .formRow {
         margin-top: 15px;
         margin-bottom: 15px;
     }
     .formRowButton {
         margin-top: 15px;
         margin-bottom: 15px;
     }
 }


 @media screen and (min-height: 1300px){
    .myForm {
        width: 80%;
        height: auto;
        border-radius: 0;
        height: 80%;
    }
    .myCard{
        height: 75%;
    }
 }

.welcomeText{
    font-family: 'Great Vibes';
    font-size: 45px;
    color:white;
    text-shadow: 2px 2px black;
    position: absolute;
    bottom: 30%;
}


@-webkit-keyframes updown {
    0%{
        bottom:20px;
    }

    50%{
        bottom:30px;
    }

    100%{
        bottom:20px;
    }
}


@keyframes updown {
    0%{
        bottom:20px;
    }

    50%{
        bottom:30px;
    }

    100%{
        bottom:20px;
    }
}


.bi-chevron-compact-down{
    -webkit-animation: updown infinite ease-in-out 2s;
            animation: updown infinite ease-in-out 2s;
    position:absolute;
}

.imgSectionWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

 .myPrivate{
    display: flex;
    flex-flow: column;
    height: 100vh;
}

.myPrivateContent{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eee;
}

.myContentSection{
    width: 80%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    background-color: #fff;
}

.formLabel{
    align-self: flex-start;
}
.myFormContent{
    display: flex;
    flex-flow: column;
    justify-content: center;
    width: 80%;
}

.myFormContent input{
    border:none;
    border-bottom: 1px solid black;
    background-color: transparent;
    outline: none;
    color: black;
}

.myFormContent button{
    font-family: 'Poppins';
    background-color:white;
    color:black;
    margin-top:20px;
    border-radius: 6px;
    border:1px solid black;
    outline:none;
    font-weight: bold;
    
}

.myFormContent button:hover{
    background-color: black;
    color:white;
} 












.myHeader{
    background-color: #ccc;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}
.myHeader span{
    color:black;
    font-family: 'Poppins';
    font-size: 30px;
}

.myLink{
    background-color: black;
    color:white;
    padding: 10px;
    border-radius: 6px;
    font-family: 'Poppins';
    margin-bottom: 10px;
}


.myLink:hover{
    text-decoration: none;
    background-color: white;
    border: 0.5px solid black;
    color: black;
    
}


@media (max-width: 576px){
    .myContentSection{
        width: 100%;
        height: 100%;
    }
}
.invitationsGrid {
    max-width: 600px;
}

.counter {
    max-width: 600px;
}

.itemsSelector{
    max-width: 600px;
}

.commandSection{
    max-width: 600px;
}

.controlButton:hover{
    background-color: #ccc;
    font-weight: bold;
}

@media (max-width:768px) {
    .invitationsGrid {
        max-width: 100%;
    }
    .itemsSelector{
        max-width: 100%;
    }
    .counter {
        max-width: 100%;
    }
    .commandSection{
        max-width: 100%;
    }
}
